'use client';

import { useState, useEffect } from 'react';
import { useLanguage } from '@/app/context/language-context';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { TrendingUp, TrendingDown, Loader2, AlertCircle } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import api from '@/lib/api';
import { formatCurrency } from '@/lib/currency';

export function AiPrediction() {
    const { t, language } = useLanguage();
    const [loading, setLoading] = useState(true);
    const [prediction, setPrediction] = useState<any>(null);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        fetchPrediction();
    }, []);

    const fetchPrediction = async () => {
        try {
            setLoading(true);
            setError(null);
            const response = await api.get('/ai/predict');
            setPrediction(response.data);
        } catch (error: any) {
            console.error('Prediction error:', error);
            setError(error.response?.data?.message || t('Failed to load prediction'));
        } finally {
            setLoading(false);
        }
    };

    return (
        <Card className="glass-card border-0 shadow-lg h-full">
            <CardHeader>
                <CardTitle className="flex items-center gap-2 text-xl">
                    <TrendingUp className="h-5 w-5 text-purple-500" />
                    {t('Expense Prediction')}
                </CardTitle>
                <CardDescription>
                    {t('AI-powered prediction for next month\'s spending.')}
                </CardDescription>
            </CardHeader>
            <CardContent>
                <div className="h-[200px] w-full">
                    <ResponsiveContainer width="100%" height="100%">
                        <AreaChart data={data}>
                            <defs>
                                <linearGradient id="colorAmount" x1="0" y1="0" x2="0" y2="1">
                                    <stop offset="5%" stopColor="#8b5cf6" stopOpacity={0.3} />
                                    <stop offset="95%" stopColor="#8b5cf6" stopOpacity={0} />
                                </linearGradient>
                            </defs>
                            <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="var(--border)" />
                            <XAxis dataKey="name" hide />
                            <Tooltip
                                contentStyle={{ backgroundColor: 'var(--background)', borderRadius: '8px', border: '1px solid var(--border)' }}
                            />
                            <Area
                                type="monotone"
                                dataKey="amount"
                                stroke="#8b5cf6"
                                strokeWidth={2}
                                fillOpacity={1}
                                fill="url(#colorAmount)"
                            />
                        </AreaChart>
                    </ResponsiveContainer>
                </div>
                <div className="mt-4 space-y-2">
                    <div className="flex items-center justify-between text-sm">
                        <span className="text-muted-foreground">{t('Predicted Total')}:</span>
                        <span className="font-bold text-foreground">11,000,000 ₫</span>
                    </div>
                    <div className="flex items-center justify-between text-sm">
                        <span className="text-muted-foreground">{t('Trend')}:</span>
                        <span className="flex items-center text-red-500 font-medium">
                            <TrendingUp className="h-3 w-3 mr-1" /> +5% vs last month
                        </span>
                    </div>
                </div>
            </CardContent>
        </Card>
    );
}
